<template>
  <section id="signin-page">
    <h1> My Profile </h1>
  </section>
</template>

<script>
import { mapState, mapGetters } from "vuex";
import mixins from "../../_utils/mixins";
import { required, minLength, email } from "vuelidate/lib/validators";

export default {
  mixins: [mixins],
  data() {
    return {
      user: {
        email: "",
        password: ""
      }
    };
  },
  validations: {
    user: {
      email: {
        required,
        email
      },
      password: {
        required,
        minLength: minLength(4)
      }
    }
  },
  computed: {
    ...mapState({
      newUser: state => state.authModule.userData,
      token: state => state.authModule.token
    }),
    ...mapGetters("authModule", {
      newUser: "getNewUserDetail"
    })
  },
  methods: {
    nav(routeName) {
      this.$router.push(routeName);
    },
    signIn(form) {
      this.$store
        .dispatch("authModule/login", this.user)
        .then(res => {
          if (res.status) {
            this.success("authSuccess");
          } else {
            this.fail("authFail");
          }
        })
        .catch(e => {
          this.fail("authFail");
        });
    }
  },
  created() {
  }
};
</script>

<style></style>
